<template>
  <div>
    <div class="Info">
      <div>
        <!-- <h2>团委概况</h2> -->
        <ul>
          <li
            v-for="(item,index) in listNav"
            :key="index"
            :class="{active:index == num}"
            @click="tab(index)"
          >{{item}}</li>
        </ul>
      </div>
      <div class="ml20" v-for="(itemCon,index) in listMain" :key="index" v-show=" index == num">
        <div v-if="show">
          <h2>标题</h2>
          {{itemCon}}
        </div>
        <div v-else>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="currentPage"
            :page-size="10"
            layout="total, prev, pager, next"
            :total="100"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  props: ["listNav", "listMain"],
  data() {
    return {
      num: 0,
      currentPage:1,
      show:true
    };
  },
  methods: {
    tab(index) {
      this.num = index;
    },
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    }
  }
};
</script>
<style lang="scss">
.Info {
  display: flex;
  display: -webkit-flex;
  & >div {
    background: #fafafa;
    border: 1px solid #f1f1f1;
    flex: 1;
    &.ml20 {
      margin-left: 20px;
      min-height: 500px;
    }
    &:first-child {
      width: 178px;
      padding: 10px;
      flex: none;
      li {
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        cursor: pointer;
        &.active {
          background: #d80a0a;
          color: #fff;
          
        }
      }
      h2 {
        color: #333;
        text-align: left;
        padding: 5px 15px;
      }
    }
  }
}
</style>
