<script>
export default {
  data() {
    return {
      input1: "",
      navBar: [
        { router: "/", name: "首页" },
        { router: "/list/TWGK", name: "团委概况" },
        { router: "/list/XSKJ", name: "学术科技" },
        { router: "/list/XYWH", name: "校园文化" },
        { router: "/list/CXCY", name: "创新创业" },
        { router: "/list/QYWH", name: "权益维护" },
        { router: "/list/WSTX", name: "网上团校" },
        { router: "/list/XSZZ", name: "学生组织" },
        { router: "/list/CYZT", name: "从严治团" }
      ],
      navItem: "",
      levelList: ""
    };
  },
  methods: {
    elSearch() {},
    getBreadcrumb() {
      let matched = this.$route.matched.filter(item => item.name);
      console.log(this.$route.matched);
      const first = matched[0];
      if (first && first.name !== "首页") {
        matched = [
          { path: "/", meta: { title: "首页" } }
        ].concat(matched);
        this.navBar.map(item=>{
          if(this.$route.path == item.router){
            matched.push({ path:item.router, meta: { title: item.name } })
          }
        })
      }
      this.levelList = matched;
    }
  },
  mounted() {
    this.getBreadcrumb();
  },
  watch: {
    $route(to, from) {
      this.getBreadcrumb();
    }
  }
};
</script>
